<template>
    <div id = "dashboard">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>Employees</h4>
            </li>
            <li v-for = "emp in employees" v-bind:key = "emp.id" class = "collection-item">
                <div class="chip">{{emp.dept}}</div>
                {{emp.employee_id}}:{{emp.name}}

                <router-link class="secondary-content" v-bind:to="{name:'view-employee',params:{employee_id:emp.employee_id}}">
                <i class="fa fa-eye"></i>
                </router-link>
            </li>
        </ul>
        <!-- Plus button  -->
        <div class="fixed-action-btn">
            <router-link to="/new" class = "btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
        <!-- Plus button  -->
    </div>
</template>

<script>
import db from './FirebaseInit'
export default {
    name: 'dashboard',
    data(){
        return{
            employees:[]
        }
    },
    created(){
        db.collection('Employee').orderBy('Dept').get().then
        (querySnapshot => {
            querySnapshot.forEach(doc=>{
                
                const data = {
                    'id':doc.id,
                    'employee_id':doc.data().Employee_id,
                    'name': doc.data().Name,
                    'dept': doc.data().Dept,
                    'position': doc.data().Position
                }

                this.employees.push(data)
                console.log('Name : '+doc.data().Name);
            })
        })
    }
}
</script>


